import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom' // Switch Route    Route url . Link      react   .( )
import Home from './routes/Home'
import Details from './routes/Details'

function App() {
 
  return (
    <Router>
      <Switch>
        <Route path="/movie/:id">
          <Details />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  )
}

export default App;
